.question {

  // .question__question-inner

  &__question-inner {
    flex: 0 1 45%;
    
  }

  // .question__title

  &__title {
    color: $black;
    margin-bottom: 25px;
  }

  // .question__question-block

  &__question-block {
    max-width: 600px;
    margin-bottom: 20px;
    cursor: pointer;
  }

  // .question__question-title

  &__question-title {
    color: $black;
    border-bottom: 1px solid black;
  }

  // .question__question-content

  &__question-content {
    display: none;
    &.active{
      display: block;
    }
  }
}
