body{
  font-family: 'Arial';
}

.question {

  // .question__container

  &__container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
  }

  // .question__inner

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width:1024px) {
      flex-direction: column;
      align-items: center;
    }
    margin-top: 30px;
  }
}
