@mixin font($font_name, $file_name, $weight, $style) {

  @font-face {
  font-family: $font_name;
  font-display: swap;
  src: url("../fonts/#{$file_name}.woff") format("woff"), url("../fonts/#{$file_name}.woff2") format("woff2");
  font-weight: #{$weight};
  font-style: #{$style};
  }
  }

$green: #6FBB84;
$yellow: #FFC522;
$orange: #F7941E;
$brown:  #755441;
$black:#2B2B2B;
$grey:#606060;
$yellowfede: #FFC522;
@import '_null';
@import '_questionstyle';
@import '_img';
@import '_qust';