.question {

  // .question__img-inner

  &__img-inner {
    display: grid;
    grid-template-columns:  1fr 1fr;
    grid-column-gap: 30px;
    grid-row-gap: 25px;
    @media (max-width:1024px){
      margin-bottom: 40px;
    }
    @media (max-width:400px) {
      grid-template-columns: 1fr;
    }
  }

  // .question__img-block

  &__img-block {
  }

  // .question__img-text

  &__img-text {
    display: block;
    width: 100%;
    text-align: center;
    font-weight: 700;
    color: $black;
    cursor: pointer;
  }
}
